body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  color: #212121;
}

.App-header {
  background-color: #eef0ef;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@media only screen and (max-width: 999px) {
  .desktop-view {
    display: none;
  }

  .phone-view {
    display: block;
  }
}

@media only screen and (min-width: 1000px) {
  .desktop-view {
    display: block;
  }

  .phone-view {
    display: none;
  }
}

.page {
  position: absolute;
  top: 100px;
  left: 0;
  right: 0;
}

.fade-appear,
.fade-enter {
  opacity: 0;
  z-index: 1;
}
.fade-appear-active,
.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 400ms linear 180ms;
}

.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active {
  opacity: 0;
  transition: opacity 150ms linear;
}

.sc-header {
  background: #52be80;
  min-height: 75px;
  border-top-left-radius: 9px;
  border-top-right-radius: 9px;
  color: white;
  padding: 10px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  position: relative;
  box-sizing: border-box;
  display: flex;
}

.restaurant {
  background-color: #fff;
  margin-top: 40px;
  margin-bottom: 0px;
  padding: 20px 20px 57px;
  border: 1px solid #fff;
  display: inline-block;
  cursor: pointer;
}

.restaurant:hover {
  border-color: #d3d5df;
  box-shadow: 0 4px 7px 0;
}

.restaurant .image {
  width: 250px;
  height: 150px;
}

.restaurant .name {
  font-weight: bold;
  margin-top: 20px;
}

.restaurant .types {
  color: grey;
  width: 70px;
}

.restaurant .rating .rating-value {
  color: white;
  display: inline-block;
  position: relative;
  bottom: 2px;
  left: 2px;
}

.restaurant .rating .star-icon {
  height: 15px;
  width: 15px;
  display: inline-block;
  position: relative;
  bottom: 4px;
}

.offers {
  border-top: 1px solid #e9e9eb;
  padding-top: 10px;
}

.offers .discount {
  height: 20px;
  width: 20px;
  display: inline-block;
}

.offers .offer-content {
  display: inline-block;
  position: "relative";
  top: 10px;
  margin: 0px 11px;
  font-size: 14px;
}

.navbar {
  background-color: #fff;
  position: fixed;
  height: 75px;
  width: 100%;
  z-index: 5;
}

.navbar .logo {
  color: #52be80;
  font-size: 20px;
  font-weight: 700;
  cursor: pointer;
}

.navbar svg {
  color: #52be80;
}

.navbar .cart {
  color: #52be80;
}

.navbar .navlink {
  font-size: 20px;
  margin-left: 5px;
  margin-right: 5px;
}

.cont {
  margin: 20px auto;
}

.homepage .filters {
  background: #fff;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
  width: 100%;
  padding-top: 100px;
}

.homepage .restaurant-count {
  margin: 10px 40px;
}

.homepage .filters-div {
  margin: 0 10px;
  position: relative;
  padding-bottom: 10px;
}

.homepage .filter {
  font-size: 16px;
  font-weight: 300;
  text-transform: capitalize;
  color: #686b78;
  margin-left: 35px;
  cursor: pointer;
  position: relative;
  display: inline-block;
}

.homepage .filter:active {
  border-bottom: 1px solid #212121;
}

.homepage svg {
  color: #52be80;
}

.cart-comp {
  height: auto;
  width: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #52be80;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 21px 36px;
  cursor: pointer;
  position: fixed;
  right: 0px;
  top: 50%;
  margin-top: -46px;
  z-index: 99;
  padding: 0px;
  border-radius: 6px 0px 0px 6px;
  border-width: 0px;
  border-style: none;
  border-style: initial;
  border-color: currentColor;
  border-color: initial;
  border-image: none;
  border-image: initial;
  outline: 0px;
  padding: 20px;
}

.cart-comp svg {
  color: #fff;
  display: inline-block;
}

.cart-comp .number {
  display: inline-block;
  color: #fff;
  font-size: 17px;
  margin-left: 5px;
}

.cart-comp .pricetag {
  width: auto;
  height: 35px;
  min-width: 80px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(255, 255, 255);
  font-family: Lato, sans-serif;
  font-size: 13px;
  font-weight: 700;
  overflow: hidden;
  border-radius: 6px;
  margin: 5px 10px 10px;
  padding-top: 4px;
  padding-left: 4px;
  padding-right: 4px;
}

.cart-comp .price {
  color: #52be80;
  font-weight: bold;
}

.cart-slider {
  width: 25%;
  height: 100%;
  background-color: white;
  position: fixed;
  top: 0;
  right: 0;
  transition: transform 0.3s
    cubic-bezier(
      calc(1 - var(--x2)),
      calc(1 - var(--y2)),
      calc(1 - var(--x1)),
      calc(1 - var(--y1))
    );
  overflow: scroll;
  z-index: 200;
  cursor: default;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 21px 36px;
  padding-left: 15px;
  padding-right: 15px;
}

.cart-slider.hide {
  transform: translate3d(-100vw, 0, 0);
}

.cart-slider.show {
  transform: translate3d(0vw, 0, 0);
  overflow: hidden;
}

.cart-slider .essential {
  padding-top: 15px;
  padding-bottom: 10px;
}

.cart-slider svg {
  color: #52be80;
  display: inline-block;
  height: 25px;
  width: 25px;
}

.cart-slider .number {
  color: #52be80;
  display: inline-block;
  font-size: 22px;
  position: relative;
  top: 4px;
}

.cart-slider .close {
  font-size: 22px;
  position: relative;
  top: 5px;
  cursor: pointer;
}

.cart-slider .checkout {
  height: 48px;
  width: calc(100% - 30px);
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #52be80;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px;
  cursor: pointer;
  padding: 0px;
  border-radius: 48px;
  border-width: 0px;
  border-style: none;
  border-style: initial;
  border-color: currentColor;
  border-color: initial;
  border-image: none;
  border-image: initial;
  outline: 0px;
  position: absolute;
  bottom: 10px;
  color: #fff;
  font-size: 22px;
  padding-left: 20px;
}

.cart-slider .checkout-btn {
  width: auto;
  height: 44px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(255, 255, 255);
  font-family: Lato, sans-serif;
  font-size: 15px;
  font-weight: 700;
  color: #52be80;
  margin-right: 2px;
  padding: 0px 30px;
  overflow: hidden;
  border-radius: 28px;
}

.cart-slider .checkout .checkoutxt {
  color: #fff;
}

.cart-item-desk {
  margin: 10px;
  display: flex;
  flex-direction: row;
}

.cart-item-desk .name {
  font-size: 17px;
  font-weight: 400;
  margin-top: 8px;
  width: 40%;
  margin-left: 10px;
}

.cart-item-desk .counter {
  margin-right: 30px;
  margin-top: 3px;
}

.cart-item-desk .price {
  margin-top: 8px;
}

.cart-item-desk .cross {
  padding-top: 3px;
  margin-left: 10px;
  font-size: 20px;
  color: #d9534f;
  cursor: pointer;
}

.cart-it .buttonCustomize {
  background-color: #52be80;
  border: none;
  border-radius: 5px;
  outline: none;
  margin-left: 40%;
}

.signinpage {
  width: 25%;
  height: 100vh;
  background-color: white;
  position: fixed;
  top: 0;
